  src: url("fonts/helvetica-neue-bold-italic.woff2") format("woff2"), url("fonts/helvetica-neue-bold-italic.woff") format("woff");
}

.bx--visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--body {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  color: #152935;
  background-color: #f5f7fa;
  line-height: 1;
}

h1 {
  font-size: 2rem;
  line-height: 1.25;
  letter-spacing: .5px;
}

h2 {
  font-size: 1.625rem;
  line-height: 1.25;
  letter-spacing: .5px;
}

h3 {
  font-size: 1.25rem;
  line-height: 1.25;
  letter-spacing: .5px;
}

h4 {
  font-size: 1.125rem;
  line-height: 1.25;
}

h5 {
  font-size: 1rem;
  line-height: 1.25;
}

h6 {
  font-size: 0.875rem;
  line-height: 1.25;
}

p {
