  line-height: 1.5;
}

strong {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: .5px;
  font-weight: 700;
}

em {
  font-style: italic;
}

a {
  color: #3d70b2;
}

button.bx--btn {
  display: inline-block;
}

button.bx--btn::-moz-focus-inner {
  padding: 0;
  border: 0;
}

button.bx--btn .bx--btn__icon {
  position: relative;
  vertical-align: middle;
  top: -1px;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  button.bx--btn .bx--btn__icon {
    top: 0;
  }
}

.bx--btn {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: .5px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  font-size: 0.875rem;
  font-weight: 700;
  height: 2.5rem;
  padding: 0 1rem;
  border-radius: 0;
  text-align: center;
  text-decoration: none;
  transition-duration: 250ms;
  transition-timing-function: ease-in;
  white-space: nowrap;
}

.bx--btn:disabled {
  cursor: not-allowed;
  opacity: .5;
}

.bx--btn .bx--btn__icon {
  width: 1rem;
  height: 1rem;
  margin-left: .5rem;
}

.bx--btn--primary {
  background-color: #3d70b2;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  color: #fff;
}

.bx--btn--primary:focus, .bx--btn--primary:hover {
  background-color: #5596e6;
}

.bx--btn--primary:focus {
  border: 1px solid #f5f7fa;
  outline: 2px solid #5596e6;
}

.bx--btn--primary:disabled:hover, .bx--btn--primary:disabled:focus {
  background-color: #3d70b2;
}

.bx--btn--primary:active {
  background-color: #234066;
}

.bx--btn--primary .bx--btn__icon {
  fill: #fff;
}

.bx--btn--secondary {
  background-color: transparent;
  border-width: 2px;
  border-style: solid;
  border-color: #3d70b2;
  color: #3d70b2;
}

.bx--btn--secondary:focus, .bx--btn--secondary:hover {
  background-color: #3d70b2;
}

.bx--btn--secondary:focus {
  border: 1px solid #f5f7fa;
  outline: 2px solid #3d70b2;
}

.bx--btn--secondary:disabled:hover, .bx--btn--secondary:disabled:focus {
  background-color: transparent;
}

.bx--btn--secondary:active {
  background-color: transparent;
}

.bx--btn--secondary .bx--btn__icon {
  fill: currentColor;
}

.bx--btn--secondary:hover, .bx--btn--secondary:focus {
  color: #fff;
}

.bx--btn--secondary:hover:disabled, .bx--btn--secondary:focus:disabled {
  color: #3d70b2;
}

.bx--btn--danger {
  background-color: transparent;
  border-width: 2px;
  border-style: solid;
  border-color: currentColor;
  color: #8c9ba5;
}

.bx--btn--danger:focus, .bx--btn--danger:hover {
  background-color: #e71d32;
}

.bx--btn--danger:focus {
  border: 1px solid #f5f7fa;
  outline: 2px solid #e71d32;
}

.bx--btn--danger:disabled:hover, .bx--btn--danger:disabled:focus {
  background-color: transparent;
}

.bx--btn--danger:active {
  background-color: transparent;
}

.bx--btn--danger .bx--btn__icon {
  fill: #8c9ba5;
}

.bx--btn--danger:hover {
  color: #fff;
  border: 2px solid transparent;
}

.bx--btn--danger:focus {
  color: #fff;
}

.bx--btn--danger:hover:disabled, .bx--btn--danger:focus:disabled {
  color: #8c9ba5;
  border: 2px solid currentColor;
}

.bx--btn--danger:hover > .bx--btn__icon,
.bx--btn--danger:focus > .bx--btn__icon {
  fill: #fff;
}

.bx--btn--sm {
  height: 2rem;
  padding: 0 0.5rem;
  letter-spacing: 0;
}

.bx--btn--secondary + .bx--btn--primary {
  margin-left: 1rem;
}

.bx--btn--copy {
  position: relative;
  overflow: visible;
}

.bx--btn--copy .bx--btn__icon {
  margin-left: 0.3125rem;
}

.bx--btn--copy__feedback {
  position: absolute;
  display: none;
  top: 1.2rem;
  left: 50%;
}

.bx--btn--copy__feedback:before {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  font-size: 0.75rem;
  top: 1.1rem;
  padding: .5rem 1rem;
  border: 1px solid #dfe3e6;
  color: #152935;
  content: attr(data-feedback);
  transform: translateX(-50%);
  white-space: nowrap;
  pointer-events: none;
}

.bx--btn--copy__feedback:after {
  top: .85rem;
  width: .5rem;
  height: .5rem;
  border-right: 1px solid #dfe3e6;
  border-bottom: 1px solid #dfe3e6;
  content: '';
  transform: rotate(-135deg);
}

.bx--btn--copy__feedback:before, .bx--btn--copy__feedback:after {
  position: absolute;
  display: block;
  background: #fff;
}

.bx--btn--copy__feedback--displayed {
  display: inline-flex;
}

.bx--fieldset {
  margin-bottom: 2rem;
}

.bx--form-item {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  flex: 1;
  margin-right: 1rem;
  margin-bottom: 1.5rem;
}

.bx--form-item:last-of-type {
  margin-right: 0;
  margin-bottom: 0;
}

.bx--label {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  font-size: 0.875rem;
  font-weight: 700;
  display: inline-block;
  vertical-align: baseline;
  margin-bottom: 0.625rem;
}

.bx--label--disabled {
  opacity: .5;
}

input[data-invalid],
textarea[data-invalid] {
  box-shadow: 0 2px 0px 0px #e71d32;
}

input[data-invalid] ~ .bx--form-requirement,
textarea[data-invalid] ~ .bx--form-requirement {
  max-height: 12.5rem;
}

.bx--form-requirement {
  font-size: 0.6875rem;
  margin: .75rem 0 0;
  max-height: 0;
  overflow: hidden;
  font-weight: 700;
  line-height: 1.5;
}

.bx--form-requirement::before {
  content: '*';
  display: inline-block;
  color: #e71d32;
}

.bx--file {
  width: 100%;
}

.bx--file-input {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--file-btn {
  display: inline-flex;
  margin: 0;
}

.bx--label-description {
  font-size: 0.875rem;
  line-height: 1.5;
  color: #5a6872;
  margin-top: .25rem;
  margin-bottom: 1.25rem;
}

.bx--file-container {
  display: block;
  width: 100%;
  margin-top: 1.25rem;
}

.bx--file__selected-file {
  display: block;
  width: 300px;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 1.875rem;
  background-color: rgba(61, 112, 178, 0.1);
  padding: 0 1rem;
  margin-bottom: 1.25rem;
}

.bx--file__selected-file:last-child {
  margin-bottom: 0;
}

.bx--file-filename {
  font-size: 0.75rem;
  display: block;
  width: 100%;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #152935;
  margin-right: 1rem;
}

.bx--file__state-container {
  display: flex;
  align-items: center;
}

.bx--file__state-container .bx--loading {
  width: 1.125rem;
  height: 1.125rem;
  margin-right: -7px;
}

.bx--file__state-container .bx--loading__svg {
  stroke: #8c9ba5;
}

.bx--file__state-container .bx--file-close,
.bx--file__state-container .bx--file-complete {
  width: 1rem;
  height: 1rem;
  fill: #152935;
  cursor: pointer;
}

.bx--file__state-container .bx--file-close {
  fill: #8c9ba5;
}

.bx--file__state-container .bx--file-complete {
  fill: #5aa700;
}

.bx--form-item.bx--checkbox-wrapper {
  margin-bottom: 1rem;
}

.bx--form-item.bx--checkbox-wrapper:first-of-type {
  margin-top: 0.375rem;
}

.bx--form-item.bx--checkbox-wrapper:last-of-type {
  margin-bottom: 0;
}

.bx--checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--checkbox-label {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.bx--checkbox-appearance {
  display: inline-block;
  height: 1.125rem;
  width: 1.125rem;
  margin-right: .5rem;
  background-color: #fff;
  border-radius: 2px;
  border: 2px solid #8c9ba5;
}

.bx--checkbox:checked + .bx--checkbox-appearance,
.bx--checkbox:checked + .bx--checkbox-label .bx--checkbox-appearance {
  display: flex;
  align-items: baseline;
  background-color: #3d70b2;
  border-color: #3d70b2;
}

.bx--checkbox:disabled + .bx--checkbox-label,
.bx--checkbox:disabled + .bx--checkbox-appearance,
.bx--checkbox:disabled ~ .bx--checkbox-label-text {
  opacity: .5;
  cursor: not-allowed;
}

.bx--checkbox:focus + .bx--checkbox-label .bx--checkbox-appearance,
.bx--checkbox:focus + .bx--checkbox-appearance {
  outline: 1px dotted;
  outline: auto 5px -webkit-focus-ring-color;
}

.bx--checkbox-checkmark {
  display: none;
  fill: #fff;
  width: 0.9375rem;
  height: 0.9375rem;
}

.bx--checkbox:checked + .bx--checkbox-appearance .bx--checkbox-checkmark,
.bx--checkbox:checked + .bx--checkbox-label .bx--checkbox-appearance .bx--checkbox-checkmark {
  display: block;
}

.bx--radio-button-group {
  display: flex;
  align-items: center;
  margin-top: 0.375rem;
}

.bx--radio-button {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--radio-button__label {
  font-size: 0.875rem;
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 1rem;
}

.bx--radio-button__appearance {
  background-color: #fff;
  border-radius: 50%;
  border: 2px solid #8c9ba5;
  height: 1.125rem;
  width: 1.125rem;
  margin-right: 0.625rem;
}

.bx--radio-button:checked + .bx--radio-button__label .bx--radio-button__appearance {
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: #3d70b2;
}

.bx--radio-button:checked + .bx--radio-button__label .bx--radio-button__appearance:before {
  content: '';
  display: inline-block;
  position: relative;
  width: .5rem;
  height: .5rem;
  border-radius: 50%;
  background-color: #3d70b2;
}

.bx--radio-button:disabled + .bx--radio-button__label {
  opacity: .5;
  cursor: not-allowed;
}

.bx--radio-button:focus + .bx--radio-button__label .bx--radio-button__appearance {
  outline: 1px dotted;
  outline: auto 5px -webkit-focus-ring-color;
}

.bx--toggle {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--toggle__label {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  position: relative;
  display: flex;
  align-items: center;
  transition-duration: 250ms;
  cursor: pointer;
  margin: 1rem 0;
}

.bx--toggle__appearance {
  position: relative;
  width: 48px;
}

.bx--toggle__appearance:before {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 4px;
  top: -2px;
  background-color: #8c9ba5;
  transition-duration: 250ms;
  cursor: pointer;
}

.bx--toggle__appearance:after {
  box-sizing: border-box;
  position: absolute;
  display: block;
  border: 2px solid #8c9ba5;
  cursor: pointer;
  top: -12px;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  content: '';
  transition-duration: 250ms;
}

.bx--toggle__text--left,
.bx--toggle__text--right {
  font-size: 0.875rem;
  position: relative;
}

.bx--toggle__text--left {
  margin-right: .5rem;
}

.bx--toggle__text--right {
  margin-left: .5rem;
}

.bx--toggle:checked + .bx--toggle__label .bx--toggle__appearance:before {
  background-color: #3d70b2;
}

.bx--toggle:checked + .bx--toggle__label .bx--toggle__appearance:after {
  transform: translateX(24px);
  background-color: #3d70b2;
  box-shadow: none;
  border-color: #3d70b2;
}

.bx--toggle:focus + .bx--toggle__label .bx--toggle__appearance:before {
  outline: 1px solid transparent;
}

.bx--toggle:focus + .bx--toggle__label .bx--toggle__appearance:after {
  box-shadow: 0 0 0 3px #7cc7ff;
  outline: 1px solid transparent;
}

.bx--toggle:disabled + .bx--toggle__label {
  cursor: not-allowed;
  opacity: .5;
}

.bx--toggle:disabled + .bx--toggle__label .bx--toggle__appearance:before, .bx--toggle:disabled + .bx--toggle__label .bx--toggle__appearance:after {
  cursor: not-allowed;
}

.bx--search {
  display: flex;
  position: relative;
  width: 100%;
}

.bx--search .bx--label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
}

.bx--search-input {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  appearance: none;
  border: none;
  background-color: rgba(61, 112, 178, 0.1);
  color: #152935;
  font-weight: 700;
  padding: 0 2.5rem 0 2.5rem;
  text-overflow: ellipsis;
  width: 100%;
}

.bx--search-input:focus {
  outline: 1px dotted;
  outline: auto 5px -webkit-focus-ring-color;
}

.bx--search-input::placeholder {
  color: #5a6872;
  font-style: italic;
  font-weight: 400;
}

.bx--search--sm .bx--search-input {
  font-size: 0.875rem;
  height: 2rem;
}

.bx--search--lg .bx--search-input {
  font-size: 0.875rem;
  height: 2.5rem;
}

.bx--search-magnifier,
.bx--search-close {
  position: absolute;
  height: 1rem;
  width: 1rem;
  top: calc(50% - .5rem);
}

.bx--search-magnifier {
  left: .75rem;
  fill: #8c9ba5;
}

.bx--search-close {
  transition: 250ms;
  fill: #8c9ba5;
  cursor: pointer;
  visibility: visible;
  opacity: 1;
}

.bx--search--sm .bx--search-close {
  right: .75rem;
}

.bx--search--lg .bx--search-close {
  right: 6.25rem;
}

.bx--search-button {
  border: 0;
  transition: 250ms;
  height: 2.5rem;
  width: 2.5rem;
  min-width: 2.5rem;
  margin-left: .25rem;
  background-color: #fff;
  position: relative;
}

.bx--search-button svg {
  transition: 250ms;
  fill: #3d70b2;
  height: 1rem;
  width: 1rem;
}

.bx--search-button:hover {
  cursor: pointer;
  background-color: #3d70b2;
}

.bx--search-button:hover svg {
  fill: #fff;
}

.bx--search-close--hidden {
  visibility: hidden;
  opacity: 0;
}

.bx--search-view--hidden {
  display: none;
}

.bx--select {
  position: relative;
  display: block;
}

.bx--select-input {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  font-size: 0.875rem;
  appearance: none;
  display: block;
  width: 100%;
  padding: .75rem 2.75rem .75rem 1rem;
  height: 2.5rem;
  color: #152935;
  background-color: rgba(61, 112, 178, 0.1);
  border: none;
  border-radius: 0;
  cursor: pointer;
}

.bx--select-input::-ms-expand {
  display: none;
}

.bx--select-input:focus {
  outline: 1px dotted;
  outline: auto 5px -webkit-focus-ring-color;
  outline: auto 2px -webkit-focus-ring-color;
}

.bx--select-input:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.bx--select__arrow {
  fill: #3d70b2;
  position: absolute;
  right: 1rem;
  top: 1.175rem;
  width: 0.625rem;
  height: 0.3125rem;
  pointer-events: none;
}

.bx--select-optgroup,
.bx--select-option {
