  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  list-style: none;
}

.bx--accordion__item {
  transition: all 250ms cubic-bezier(0.5, 0, 0.1, 1);
  border-top: 1px solid #dfe3e6;
  overflow: hidden;
}

.bx--accordion__item:last-child {
  border-bottom: 1px solid #dfe3e6;
}

.bx--accordion__heading {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: .5rem 0;
}

.bx--accordion__arrow {
  transition: all 250ms cubic-bezier(0.5, 0, 0.1, 1);
  height: .75rem;
  width: .75rem;
  fill: #8c9ba5;
}

.bx--accordion__title {
  margin: 0;
  margin-left: 1rem;
}

.bx--accordion__content {
  transition: all 250ms cubic-bezier(0.5, 0, 0.1, 1);
  padding: 0 1rem 0 1.75rem;
  height: 0;
  visibility: hidden;
  opacity: 0;
}

.bx--accordion__content p {
  font-size: 0.875rem;
}

.bx--accordion__item--active {
  overflow: initial;
}

.bx--accordion__item--active .bx--accordion__content {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
  height: auto;
  visibility: visible;
  opacity: 1;
}

.bx--accordion__item--active .bx--accordion__arrow {
  transform: rotate(90deg);
  fill: #3d70b2;
}

.bx--progress {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  display: flex;
  list-style: none;
}

.bx--progress-step {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  flex: 1;
  min-width: 7rem;
  transition: 250ms all cubic-bezier(0.5, 0, 0.1, 1);
  overflow: visible;
}

.bx--progress-line {
  position: absolute;
  top: .625rem;
  right: 99.5%;
  height: 1px;
  width: calc(100% - 24px);
  border: 1px inset transparent;
}

.bx--progress-step:first-child .bx--progress-line {
  display: none;
}

.bx--progress-step svg {
  position: relative;
  z-index: 1;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  margin-bottom: .5rem;
}

.bx--progress-label {
  line-height: 1;
  width: 75%;
}

.bx--progress-step--current circle:first-child {
  stroke: #3d70b2;
  stroke-width: 5;
  fill: transparent;
}

.bx--progress-step--current circle:last-child {
  fill: #3d70b2;
}

.bx--progress-step--current .bx--progress-label {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #3d70b2;
  font-weight: 700;
}

.bx--progress-step--current .bx--progress-line {
  background-color: #3d70b2;
}

.bx--progress-step--incomplete circle {
  stroke: #8c9ba5;
  stroke-width: 5;
  fill: transparent;
}

.bx--progress-step--incomplete .bx--progress-label {
  color: #8c9ba5;
}

.bx--progress-step--incomplete .bx--progress-line {
  background-color: #8c9ba5;
}

.bx--progress-step--complete circle {
  stroke: #3d70b2;
  stroke-width: 5;
  fill: transparent;
}

.bx--progress-step--complete polygon {
  fill: #3d70b2;
}

.bx--progress-step--complete .bx--progress-label {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #3d70b2;
  font-weight: 700;
}

.bx--progress-step--complete .bx--progress-line {
  background-color: #3d70b2;
}

.bx--toolbar {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 1rem 0;
}

.bx--toolbar > div {
  margin: 0 0.25rem;
}

.bx--toolbar .bx--search-input {
  height: 2rem;
  background-color: transparent;
  outline: none;
}

.bx--toolbar .bx--search-close {
  display: none;
}

.bx--toolbar .bx--overflow-menu__icon {
  fill: #5a6872;
  transition: fill 50ms cubic-bezier(0.5, 0, 0.1, 1);
}

.bx--toolbar .bx--search-magnifier {
  fill: #5a6872;
  transform: scale(1.3);
  transition: all 175ms cubic-bezier(0.5, 0, 0.1, 1);
  top: 0.5rem;
  left: 0.375rem;
  cursor: pointer;
}

.bx--toolbar fieldset {
  border: 0;
  padding: 0;
}

.bx--toolbar .bx--toolbar-search--active {
  width: 15.625rem;
}

.bx--toolbar .bx--toolbar-search--active .bx--search-magnifier {
  transform: scale(1);
  top: 0.5625rem;
}

.bx--toolbar .bx--toolbar-search--active .bx--search-input {
  background-color: rgba(61, 112, 178, 0.1);
}

.bx--toolbar .bx--toolbar-search--active .bx--search-close {
  display: block;
}

.bx--toolbar .bx--checkbox-label {
  margin-bottom: 0;
}

.bx--toolbar .bx--overflow-menu--open > .bx--overflow-menu__icon {
  fill: #3d70b2;
}

.bx--toolbar-search {
  width: 1.8rem;
  transition: all 175ms cubic-bezier(0.5, 0, 0.1, 1);
}

.bx--toolbar-search__btn {
  position: absolute;
  left: 0;
  top: 0;
  background: transparent;
  border: 0;
  height: 2rem;
  width: 2rem;
}

.bx--toolbar-filter-icon {
  width: 1.2rem;
}

.bx--toolbar-menu__title {
  font-size: 0.75rem;
  letter-spacing: .5px;
  font-weight: 700;
  padding: .5rem 1.25rem;
}

.bx--toolbar-menu__option {
  padding: .5rem 1.25rem;
}

.bx--toolbar-menu__divider {
  width: 100%;
  border: 0;
  border-top: 1px solid #dfe3e6;
}

.bx--radio-button-group {
  border: none;
}

.bx--footer--bottom-fixed {
  position: fixed;
  bottom: 0;
  left: 0;
}

.bx--footer {
  font-family: 'IBM Helvetica', Helvetica Neue, HelveticaNeue, Helvetica, sans-serif;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  border-top: 2px solid #3d70b2;
  background-color: #fff;
  min-height: 3.5rem;
  z-index: 5000;
  padding: 1.25rem 5%;
  width: 100%;
}

.bx--footer-info {
  display: flex;
}

@media screen and (max-width: 600px) {
  .bx--footer-info {
    flex-direction: column;
  }
}

.bx--footer-info__item {
  font-size: 1.125rem;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-right: 4rem;
}

.bx--footer-info__item > .bx--link {
  font-weight: 700;
}

.bx--footer-info__item > .bx--footer-label {
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 0;
}

@media screen and (max-width: 600px) {
  .bx--footer-info__item > .bx--footer-label {
    display: none;
  }
}

.bx--footer-cta {
  margin-left: auto;
}

.bx--fab {
  transform: rotate(0);
  transition: transform 250ms;
  transform-origin: center;
  display: inline-block;
  width: 4.5rem;
  height: 4.5rem;
  text-decoration: none;
  filter: drop-shadow(0px 3px 3px 0 rgba(0, 0, 0, 0.1));
}

.bx--fab__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
  color: #fff;
}

.bx--fab__svg {
  width: 100%;
}

.bx--fab__svg .bx--fab__hexagon {
  transition: fill 250ms;
  fill: #5596e6;
}

.bx--fab__svg .bx--fab__plus-icon {
  transform: rotate(0);
  transition: transform 250ms;
  transform-origin: center;
  fill: #fff;
}

.bx--fab[data-state='closed'] {
  transform: rotate(90deg);
  transition: transform 250ms;
  transform-origin: center;
}

.bx--fab[data-state='closed'] .bx--fab__hexagon {
  transition: fill 250ms;
  fill: #42535c;
}

.bx--fab[data-state='closed'] .bx--fab__plus-icon {
  transform: rotate(-45deg);
  transition: transform 250ms;
  transform-origin: center;
}

