@import 'import-once';

@mixin css-helpers {
  .bx--visually-hidden {
    position:absolute;
    width:1px;
    height:1px;
    padding:0;
    margin:-1px;
    overflow:hidden;
    clip:rect(0, 0, 0, 0);
    border:0;
    visibility: visible;
    white-space: nowrap;
  }
}

@include exports('css--helpers') {
  @if global-variable-exists('css--helpers') and $css--helpers == true {
    @include css-helpers;
  }
}
